import React from 'react';
import { ResponsiveLine } from '@nivo/line'
import moment from 'moment';

const CustomSymbol = ({ size, color, borderWidth, borderColor }) => (
    <g>
        <circle fill="#fff" r={size / 2} strokeWidth={borderWidth} stroke={borderColor} />
        <circle
            r={size / 5}
            strokeWidth={borderWidth}
            stroke={borderColor}
            fill={color}
            fillOpacity={0.35}
        />
    </g>
)


// make sure parent container have a defined height when using
// responsive component, otherwise height will be 0 and
// no chart will be rendered.
// website examples showcase many properties,
// you'll often use just a few of them.
const SessionDurationResponsiveLine = ({ data }) => {

    const getMaxAveTime = [].concat(data).sort((a, b) => a.data.y < b.data.y ? 1 : -1).slice(0, 1).map((rows, index) => {
        return rows.data[0].y
    });

    let roundTime = (time, minutesToRound) => {
        if (time) {
            // console.log(">>>> time", time);
            // console.log(">>>> minutes to round", minutesToRound);
            let [hours, minutes, seconds] = time.split(':');
            hours = parseInt(hours);
            minutes = parseInt(minutes);
            seconds = parseInt(seconds);

            // Convert hours and minutes to time in minutes
            time = (hours * 60) + minutes;

            let rounded = Math.ceil(time / minutesToRound) * minutesToRound;
            let rHr = '' + Math.floor(rounded / 60)
            let rMin = '' + rounded % 60
            let rSec = '' + rounded % 60

            return rHr.padStart(2, '0') + ':' + rMin.padStart(2, '0') + ':' + rSec.padStart(2, '0')
        }
    }

    return (<ResponsiveLine
        data={data}
        margin={{ top: 20, right: 55, bottom: 45, left: 80 }}
        xScale={{ type: 'point' }}
        yScale={{
            type: "time",
            format: "%H:%M:%S",
            precision: "second",
            max: '00:00:00',
            min: roundTime(getMaxAveTime[0], 10),
            useUTC: false,
            stacked: false,
            reverse: false
        }}
        curve="monotoneX"
        enablePointLabel={true}
        axisTop={null}
        axisRight={null}
        axisBottom={{
            orient: 'top',
            tickSize: 5,
            tickPadding: 5,
            tickRotation: 25,
            legendOffset: 36,
            legendPosition: 'middle'
        }}
        yFormat="time:%Hh %Mm %Ss"
        axisLeft={{
            orient: 'left',
            format: "%H:%M:%S",
            tickSize: 10,
            tickPadding: 5,
            tickRotation: 0,
            legend: 'Avg. Session-Duration',
            legendOffset: -70,
            legendPosition: 'middle',
        }}
        colors={{ scheme: 'nivo' }}
        pointSymbol={CustomSymbol}
        pointSize={10}
        pointColor={{ theme: 'background' }}
        pointBorderWidth={1}
        pointBorderColor={{ from: 'serieColor' }}
        pointLabel="Avg. Session-Duration"
        pointLabelYOffset={-12}
        useMesh={true} // Use a voronoi mesh to detect mouse interactions, enableSlices must be disabled
        enableSlices={'x'}
        sliceTooltip={({ slice }) => {
            return (
                <div
                    style={{
                        background: "var(--light-pink)",
                        padding: '0.1rem 1rem',
                        border: '1px solid var(--dark-brown)',
                        borderRadius: '5px'
                    }}
                >
                    {slice.points.map(point => (
                        <div
                            key={point.id}
                            style={{
                                color: "var(--dark-brown)",
                                padding: '0.1rem 0',
                            }}
                        >
                            <div> <strong>Date:</strong> {point.data.x}</div>
                            <strong>{point.serieId}:</strong> {
                                point.data.yFormatted
                            }
                        </div>
                    ))}
                </div>
            )
        }}
    />)
};

export default SessionDurationResponsiveLine;